body {
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
    overflow-X: hidden;
    font-family: 'Montserrat', sans-serif;
    user-select: none;
    color: #fff;
    line-height: 1.4;
    background: #1D1D1D;
}

.hide {
    display: none;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

ul,
li {
    list-style: none;
}

a {
    text-decoration: none;
    color: #fff;
}

p {
    word-break: break-word;
}

.container {
    width: 100%;
    max-width: 1200px;
    height: 100vh;
    margin: 0px auto;
    padding: 0 20px;
    position: relative;
    z-index: 5;
}

.section {
    width: 100%;
    height: 100vh;
    padding-top: 110px;
}

.section__title {
    color: #fff;
}

.area {
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: 1;
}

.area__circles {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    overflow: hidden;
}

.area__circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.1);
    animation: animateAreaCircle 25s linear infinite;
    bottom: -150px;
    
}

.area__circles li:nth-child(1){
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}


.area__circles li:nth-child(2){
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.area__circles li:nth-child(3){
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 4s;
}

.area__circles li:nth-child(4){
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.area__circles li:nth-child(5){
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
}

.area__circles li:nth-child(6){
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
}

.area__circles li:nth-child(7){
    left: 35%;
    width: 150px;
    height: 150px;
    animation-delay: 7s;
}

.area__circles li:nth-child(8){
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.area__circles li:nth-child(9){
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.area__circles li:nth-child(10){
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}



@keyframes animateAreaCircle {
    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}

@media (max-width: 376px) {
    .section__title {
        font-size: 25px;
    }
}