.section__title {
    margin-bottom: 30px;
}

.skills__list {
    width: 100%;
    height: 65vh;
    overflow-x: hidden;
    display: flex;
    flex-wrap: wrap;
}

.skills__list::-webkit-scrollbar {
    width: 6px;
}

.skills__list::-webkit-scrollbar-track {
    border-radius: 20px;
    background: #111;
}

.skills__list::-webkit-scrollbar-thumb {
    background-color: #08FDD8;
    border-radius: 20px;
}

.skills__item {
    height: 40vh;
    border-radius: 20px;
    margin: 10px;
    cursor: pointer;
    color: #fff;
    flex: 15% 1;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #111;
    position: relative;
    transition: all .7s ease;
}

.skills__item--active {
    flex: 25% 1;
}

.skills__icon {
    max-width: 60%;
    max-height: 60%;
}

.skills__title {
    position: absolute;
    font-size: 20px;
    bottom: 20px;
    left: 20px;
    opacity: 0;
    line-height: 1;
    padding-top: 5px;
    padding-right: 10px;
    border-radius: 10px;
    background-color: rgba(17, 17, 17, 0.7);
}

.skills__item--active .skills__title {
    opacity: 1;
    transition: opacity .3s ease-in .4s;
}

.skills__loader {
    font-size: 25px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    opacity: 1;
    transition: opacity .2s ease;
    -webkit-animation: loaderOpacity 2s linear infinite;
            animation: loaderOpacity 2s linear infinite;
}

.skills__loader--closed {
    opacity: 0;
    -webkit-animation: none;
            animation: none;
}

@-webkit-keyframes loaderOpacity {
    0% {
        opacity: 1;
    }

    50%{
        opacity: .6;
    }

    100%{
        opacity: 1;
    }
}

@keyframes loaderOpacity {
    0% {
        opacity: 1;
    }

    50%{
        opacity: .6;
    }

    100%{
        opacity: 1;
    }
}

