.menu {
    position: absolute;
    width: 100%;
    height: 100vh;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    transition: all .7s ease-in;
    z-index: 10;
    background: linear-gradient(45deg, #1B312E, #1d1d1d,  #1B312E);
	background-size: 2000% 100%;
	-webkit-animation: gradient 7s ease infinite;
	        animation: gradient 7s ease infinite;
	height: 100vh;
}
@-webkit-keyframes gradient {
	0% {
		background-position: 0% 0%;
	}
	50% {
		background-position: 100% 25%;
	}
	100% {
		background-position: 0% 0%;
	}
}
@keyframes gradient {
	0% {
		background-position: 0% 0%;
	}
	50% {
		background-position: 100% 25%;
	}
	100% {
		background-position: 0% 0%;
	}
}

.menu__list {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
}

.menu--open {
    transition: all .7s ease;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}

.menu__link {
    font-size: 30px;
    margin: 25px 0;
    line-height: 1;
    transition: all 1.3s ease;
}

.menu__link:nth-child(1) {
    -webkit-transform: translateY(-1000px);
            transform: translateY(-1000px);
}

.menu__link:nth-child(2) {
    -webkit-transform: translateY(-800px);
            transform: translateY(-800px);
}

.menu__link:nth-child(3) {
    -webkit-transform: translateY(-600px);
            transform: translateY(-600px);
}

.menu__link:nth-child(4) {
    -webkit-transform: translateY(-400px);
            transform: translateY(-400px);
}

.menu__link .active {
    margin: 25px 0;
    position: relative;
}

.menu__link .active::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 4px;
    border-radius: 10px;
    background-color: #08FDD8;
    bottom: -5px;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}

.menu--open .menu__link {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}

.menuBtn {
    position: absolute;
    width: 40px;
    height: 30px;
    top: 30px;
    right: 30px;
    z-index: 11;
    cursor: pointer;
}

.menuBtn__line-top {
    width: 100%;
    height: 5px;
    border-radius: 5px;
    position: absolute;
    top: 0;
    background-color: #fff;
    transition: all .3s ease;
}

.menuBtn__line-middle {
    width: 100%;
    height: 5px;
    border-radius: 5px;
    position: absolute;
    top: 50%;
    background-color: #fff;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    transition: opacity .1s ease;
}

.menuBtn__line-bottom {
    width: 100%;
    height: 5px;
    border-radius: 5px;
    position: absolute;
    bottom: 0;
    background-color: #fff;
    transition: all .3s ease;
}

.menu--open .menuBtn__line-top {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    top: 12.5px;
}

.menu--open .menuBtn__line-middle {
    opacity: 0;
}

.menu--open .menuBtn__line-bottom {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    bottom: 12.5px;
}

@media (max-width: 420px) {
    .menu__link {
        font-size: 30px;
        margin: 30px 0px;
    }
}

@media (max-width: 376px) {
    .menu__link {
