        margin: 30px 0px;
    }
}
.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(45deg, #1d1d1d, #08FDD8);
    background-size: 2000% 10%;
    position: fixed;
    z-index: 12;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    transition: all 1.5s ease 1.85s;
}

.loader--open {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.loader__ring {
    display: inline-block;
    width: 120px;
    height: 120px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    transition: all .2s ease;
}

.loader__ring:after {
    content: " ";
    display: block;
    width: 80px;
    height: 80px;
    margin: 8px;
    border-radius: 50%;
    border: 8px solid #fff;
    border-color: #fff transparent #fff transparent;
    -webkit-animation: loader__ring 1.2s linear infinite;
            animation: loader__ring 1.2s linear infinite;
}

.loader__ring--closed {
    opacity: 0;
}

.loader__title {
    padding: 0 50px;
    font-weight: normal;
    text-align: center;
    opacity: 0;
    transition: opacity .2s ease;
}

.loader__title--show {
    opacity: 1;
}

@-webkit-keyframes loader__ring {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes loader__ring {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@media (max-width: 420px) {
    .loader__title {
        font-size: 25px;
        margin: 30px 0px;
    }
}

@media (max-width: 376px) {
    .loader__title {
        font-size: 20px;
    }
}
.translator {
    position: absolute;
    left: 30px;
    top: 20px;
    max-width: 100%;
    height: 50px;
    overflow: hidden;
    display: flex;
    align-items: center;
    z-index: 6;
}

.translator__icon {
    cursor: pointer;
    width: 45px;
    margin-right: 10px;
    transition: opacity .3s ease;
}

.translator__icon:hover {
    opacity: 0.8;
}

.translator__list {
    display: flex;
}

.translator__list-item {
    margin: 0 5px;
    z-index: 1;
    -webkit-transform: translateY(-200%);
            transform: translateY(-200%);
    transition: all .4s ease;
}

.translator__list-item .active {
    opacity: 0.8;
    position: relative;
    transition: all .3s ease;
}

.translator__list-item .active:before {
    content: "";
    width: 40%;
    height: 2px;
    background-color: #fff;
    position: absolute;
    border-radius: 5px;
    bottom: -5px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    opacity: 1;
    -webkit-animation: transItemActive .2s ease;
            animation: transItemActive .2s ease;
}

@-webkit-keyframes transItemActive {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes transItemActive {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.translator--open .translator__list-item {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
