@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@500;600&display=swap');

.home {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    letter-spacing: 0.8px;
}

.home__title {
    color: #08FDD8;
    font-family: 'Oswald', sans-serif;
    font-size: 70px;
    letter-spacing: 0px;
    margin-bottom: 5px;
    animation: letterSpacingAnim 5s ease infinite;
}

.home__subtitle {
    color: #fff;
    font-size: 20px;
    animation: letterSpacingAnim 5s ease infinite;
}

@media (max-width: 660px) {
    .home__title {
        font-size: 40px;
        margin-bottom: 20px;
    }

    .home__subtitle {
        font-size: 18px;
    }
}

@media (max-width: 420px) {
    .home__title {
        font-size: 35px;
        margin-bottom: 20px;
        line-height: 1;
    }

    .home__subtitle {
        font-size: 16px;
        line-height: 1.3;
    }
}

@media (max-width: 376px) {
    .home__title {
        font-size: 38px;
        line-height: 1;
    }

    .home__subtitle {
        font-size: 12px;
    }
}