.section__title {
    margin-bottom: 30px;
}

.skills__list {
    width: 100%;
    height: 65vh;
    overflow-x: hidden;
    display: flex;
    flex-wrap: wrap;
}

.skills__list::-webkit-scrollbar {
    width: 6px;
}

.skills__list::-webkit-scrollbar-track {
    border-radius: 20px;
    background: #111;
}

.skills__list::-webkit-scrollbar-thumb {
    background-color: #08FDD8;
    border-radius: 20px;
}

.skills__item {
    height: 40vh;
    border-radius: 20px;
    margin: 10px;
    cursor: pointer;
    color: #fff;
    flex: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #111;
    position: relative;
    transition: all .7s ease;
}

.skills__item--active {
    flex: 25%;
}

.skills__icon {
    max-width: 60%;
    max-height: 60%;
}

.skills__title {
    position: absolute;
    font-size: 20px;
    bottom: 20px;
    left: 20px;
    opacity: 0;
    line-height: 1;
    padding-top: 5px;
    padding-right: 10px;
    border-radius: 10px;
    background-color: rgba(17, 17, 17, 0.7);
}

.skills__item--active .skills__title {
    opacity: 1;
    transition: opacity .3s ease-in .4s;
}

.skills__loader {
    font-size: 25px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
    transition: opacity .2s ease;
    animation: loaderOpacity 2s linear infinite;
}

.skills__loader--closed {
    opacity: 0;
    animation: none;
}

@keyframes loaderOpacity {
    0% {
        opacity: 1;
    }

    50%{
        opacity: .6;
    }

    100%{
        opacity: 1;
    }
}

@media (max-width: 1100px) {
    .skills__item {
        height: 40vh;
        flex: 20%;
    }

    .skills__item--active {
        flex: 30%;
    }
}

@media (max-width: 910px) {
    .skills__item {
        height: 40vh;
        flex: 20%;
    }

    .skills__item--active {
        flex: 30%;
    }
}

@media (max-width: 855px) {
    .skills__item {
        height: 30vh;
        flex: 20%;
    }

    .skills__item--active {
        flex: 25%;
    }
}

@media (max-width: 660px) {
    .skills__item {
        height: 35vh;
        flex: 25%;
    }

    .skills__item--active {
        flex: 35%;
    }
}

